.Wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0 25px 0 15px;
  box-sizing: border-box;
  border: 1px solid transparent;

  @media(min-width: 768px){
    padding: 0 42px 0 32px; 
  }

  @media(min-width: 1024px){
    padding: 0 70px 0 60px;
  }

  @media(min-width: 1170px){
    padding: 0;
  }
}